<template>
    <div class="elevation-5 wrap-content mx-auto">
            <h1 class="blue--text text--darken-1 text-xs-center mt-5">Contact</h1>
            <v-layout justify-center row wrap>
                <v-flex xs12 md7 class="ma-4">
                    <v-card class="pa-4 border-rounded mb-5 slide-in-left-25">
                        <iframe src="https://www.google.com/maps/embed" width="100%" height="325" frameborder="0" style="background-color: #1E88E5 !important; border:0;" allowfullscreen></iframe>
                        <v-card-title class="pl-0"><h2 class="blue--text text--darken-1">Contact Form</h2></v-card-title>
                        <p class="blue--text text--darken-1">We look forward to hearing from you and welcoming you to a personal consultation and appointment.</p>
                        <v-form
                            ref="form"
                            v-model="valid"
                            lazy-validation
                        >
                            <v-radio-group v-model="gender" :rules="genderRules" required>
                                <v-radio
                                    label="Mr."
                                    value="Mr."
                                ></v-radio>
                                <v-radio
                                    label="Mrs."
                                    value="Mrs."
                                ></v-radio>
                            </v-radio-group>

                            <v-text-field
                            v-model="firstName"
                            :rules="fNameRules"
                            label="First name"
                            required
                            ></v-text-field>

                            <v-text-field
                            v-model="lastName"
                            :rules="lNameRules"
                            label="Last name"
                            required
                            ></v-text-field>

                            <v-text-field
                            v-model="email"
                            :rules="emailRules"
                            label="E-mail"
                            required
                            ></v-text-field>

                            <v-text-field
                            v-model="phone"
                            mask="phone"
                            :rules="phoneRules"
                            label="Phone"
                            required
                            ></v-text-field>

                            <h3 class="mx-auto blue--text text--darken-1 mt-4 mb-2">Preferences</h3>
                            <div>
                                <v-checkbox
                                    label="Mon"
                                    hide-details
                                    v-model="preferences.mon"
                                />
                                <v-checkbox
                                    label="Tue"
                                    hide-details
                                    v-model="preferences.tue"
                                />
                                <v-checkbox
                                    label="Wed"
                                    hide-details
                                    v-model="preferences.wed"
                                />
                                <v-checkbox
                                    label="Thu"
                                    hide-details
                                    v-model="preferences.thu"
                                />
                                <v-checkbox
                                    label="Fri"
                                    hide-details
                                    v-model="preferences.fri"
                                />
                                <v-checkbox
                                    label="Morning"
                                    hide-details
                                    v-model="preferences.morning"
                                />
                                <v-checkbox
                                    label="Afternoon"
                                    hide-details
                                    v-model="preferences.afternoon"
                                />
                            </div>

                            <h3 class="mx-auto blue--text text--darken-1 mt-5 mb-2">Information about the appointment</h3>
                            <div>
                                <v-checkbox
                                    label="Toothache"
                                    hide-details
                                    v-model="info.toothache"
                                />
                                <v-checkbox
                                    label="Check-up"
                                    hide-details
                                    v-model="info.checkup"
                                />
                                <v-checkbox
                                    label="Prevention"
                                    hide-details
                                    v-model="info.prevention"
                                />
                                <v-checkbox
                                    label="Consultation"
                                    hide-details
                                    v-model="info.consultation"
                                />
                            </div>

                            <v-textarea
                            label="Your message"
                            v-model="yourMessage"
                            class="mt-4"
                            ></v-textarea>

                            <v-checkbox
                                class="mt-3 mb-3"
                                label="I accept the privacy rules"
                                hide-details
                                required
                                :rules="privacyRules"
                            />

                            <v-btn
                            :disabled="!valid"
                            color="blue darken-1"
                            class="text-capitalize white--text ml-0 border-rounded"
                            @click="validate"
                            >
                            Send
                            </v-btn>
                        </v-form>
                    </v-card>
                </v-flex>
                <v-flex xs12 md3 class="ma-4">
                    <v-card class="pb-3 white border-rounded slide-in-right">
                        <v-card-title><h2 class="mx-auto blue--text text--darken-1">Office hours</h2></v-card-title>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Mon 08:00 AM - 6:00 PM</h4>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Tue 08:00 AM - 6:00 PM</h4>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Wed 08:00 AM - 6:00 PM</h4>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Thu 08:00 AM - 6:00 PM</h4>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Fri 08:00 AM - 6:00 PM</h4>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Sat by appointment</h4>
                    </v-card>
                    <v-card class="mt-4 pb-3 white border-rounded slide-in-right-15">
                        <v-card-title><h2 class="mx-auto blue--text text--darken-1">Address</h2></v-card-title>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Dental Practice John Doe</h4>
                        <a href="http://maps.google.com/?q=Example 22, 14050 Berlin" target="_blank">
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Example 22</h4>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">14050 Berlin</h4>
                        </a>
                        <br>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1"><img src="./../assets/phone.png" class="mr-1" style="position: relative; top: 3px;"><a class="mb-1 text-xs-center blue--text text--darken-1" href="tel:+55 55 5555555">+55 55 5555555</a></h4>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1"><i class="fa fa-envelope blue--text text--darken-1 mr-2"></i><a class="mb-1 text-xs-center blue--text text--darken-1" href="mailto:me@example.com">me@example.com</a></h4>
                    </v-card>
                    <v-card class="mt-4 px-4 pb-3 white border-rounded slide-in-right-2">
                        <v-card-title><h2 class="mx-auto blue--text text--darken-1">We are here:</h2></v-card-title>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Our dental practice is situated directly in Berlin.</h4> <br>
                        <h4 class="mb-1 text-xs-center blue--text text--darken-1">Next subway stations are Example-Platz and Another-Example.</h4>
                    </v-card>
                </v-flex>
            </v-layout>
         <footer-comp/>
    </div>
</template>

<script>
export default {
data: () => ({
      valid: true,
      gender: '',
      firstName: '',
      lastName: '',
      email: '',
      phone: '',
      preferences: {
          mon: false,
          tue: false,
          wed: false,
          thu: false,
          fri: false,
          morning: false,
          afternoon: false
      },
      info: {
          toothache: false,
          checkup: false,
          prevention: false,
          consultation: false
      },
      yourMessage: '',
      genderRules: [
        v => !!v || 'This is required'
      ],
      fNameRules: [
        v => !!v || 'First name is required'
      ],
      lNameRules: [
        v => !!v || 'Last name is required'
      ],
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      phoneRules: [
        v => !!v || 'Phone is required'
      ],
      privacyRules: [
        v => !!v || 'This is required'
      ]
    }),

    methods: {
      callTrigger() {
        window.location.href='tel:030-302-4588'
      },
      validate () {
        if (this.$refs.form.validate()) {
          this.snackbar = true
        }
      }
    }
}
</script>

<style lang="sass">
.move-left-middle
    @media screen and (max-width: 1800px) and (min-width: 1550px)
        position: relative
        left: 25px

.wrap-content
    width: 60vw
    height: 100%
    @media screen and (max-width: 1550px) and (min-width: 1500px)
        width: 80vw
    @media screen and (max-width: 1500px)
        width: 100vw
        
.border-rounded
    border-radius: 10px !important
</style>
